.modal-success {
  width: 320px;

  @media (min-width: @tablet-width) {
    width: 640px;
  }
}

.modal-success__text-container {
  margin: 39px 39px 30px 37px;
}

.modal-succes__button-container {
  background: #f2f2f2;
  padding: 20px;
  display: flex;
  justify-content: center;
  align-items: center;

  @media (min-width: @tablet-width) {
    padding-top: 44px;
    padding-bottom: 44px;
  }
}

.modal-success__button {
  padding: 18px 62px 14px 60px;
}
